import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchColor } from '../../actions/searchAction';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';
import { filtersStylesPattern } from '../../CustomMaterialUIStyles/FilterComponents';

import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InputLabel from '@material-ui/core/InputLabel';

import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';

import RadioButtonChecked from '@material-ui/icons/RadioButtonChecked';
import RadioButtonUnchecked from '@material-ui/icons/RadioButtonUnchecked';
import CheckBox from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlank from '@material-ui/icons/CheckBoxOutlineBlank';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Label from '@material-ui/icons/Label';
import LabelOutline from '@material-ui/icons/LabelOutline';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

// import Input from '@material-ui/core/Input';
import Divider from '@material-ui/core/Divider';

import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import FormLabel from '@material-ui/core/FormLabel';
import FormGroup from '@material-ui/core/FormGroup';

const styles = theme => (filtersStylesPattern(theme))

class ImgColor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      openDialog: false,
    }
    this.handleChange = this.handleChange.bind(this);
    this.selectionRenderer = this.selectionRenderer.bind(this);
  }

  static getDerivedStateFromProps = (props, state) => {
    if (props.showFiltersBar !== true) {
      state.open = false
    }
    return null
  }

  selectionRenderer = (native=false) => {
    let values = this.props.color;
    let { classes } = this.props;
    if (!native) {
      switch (values.length) {
        case 0:
          return '';
        case 1:
          if (values[0] === null) {
            return (<span>color: <span className={classes.selectedValue}>all</span></span>)
          }
          return (<span>color: <span className={classes.selectedValue}>{values[0]}</span></span>);
        default:
          return (<span>color: <span className={classes.selectedValue}>{values.join(', ')}</span></span>);
      }
    } else {
      switch (values.length) {
        case 0:
          return '';
        case 1:
          if (values[0] === null) {
            return (<span className={classes.selectedValue}>all</span>)
          }
          return (<span className={classes.selectedValue}>{values[0]}</span>);
        default:
          return (<span className={classes.selectedValue}>{values.join(', ')}</span>);
      }
    }
  }

  handleChange = (e, list=false) => {
    // console.log('handleChange, e.target.value', e.target.value);
    let val = list ? e : e.target.value;
    if (val.length === 0) {
      this.props.fetchColor([null])
      console.log('handleChange, val.length === 0', val.length);
    } else {  
      console.log('handleChange, val.length !== 0', val.length)
      switch (true) {
        case ((val[0] === null) && (val[1] !== 'undefined')):
          this.props.fetchColor([val[1]])
          break;
        case (val[val.length - 1] === null):
          this.props.fetchColor([null])
          break;
        default:
          this.props.fetchColor(val)
      }
      this.props.fetchImages()
    }
  }

  handleListHeaderClick = () => {
    this.setState({ open: !this.state.open });
  };

  handleListItemClick = (val) => {
    let { color } = this.props;
    let index = color.indexOf(val);
    if (index === -1) {
      console.log('handleListItemClick, (index === -1) true, val', val)
      color.push(val);
      this.handleChange(color, true) 
      console.log('handleListItemClick, color', color)    
    } else {
      console.log('handleListItemClick, (index === -1) false, val', val)
      color.splice(index, 1);
      this.handleChange(color, true);
      console.log('handleListItemClick, color', color)   
    }
    this.forceUpdate()
  }

  handleDialogOpen = () => {
    console.log('handleClickOpen', this.state.openDialog)
    this.setState({
      openDialog: !this.state.openDialog
    })
  }

  render() {
    const { classes, showFiltersBar, color } = this.props;

    const options = ["grayscale", "transparent", "red", "orange", "yellow", "green",
    "turquoise", "blue", "lilac", "pink", "white", "gray", "black", "brown"];

    const optionsWithColor = new Map([
      ["grayscale", '#708090'], 
      ["transparent", 'transparent'],
      ["red", '#f00'],
      ["orange", '#ff8c00'],
      ["yellow", '#ff0'],
      ["green", '#008000'],
      ["turquoise", '40e0d0'],
      ["blue", '#00f'],
      ["lilac", '#c8a2c8'],
      ["pink", '#ff1493'],
      ["white", 'white'],
      ["gray", '#808080'],
      ["black", '#000'],
      ["brown", '#8B4513'],
    ])

    const SetIcon = (x) => {
      if (color && (color.indexOf(x) > -1)) {
        return <CheckBox classes={{ root:classes.MenuItemIconMultipleSelection }} />
      } else {
        return <CheckBoxOutlineBlank classes={{ root: classes.MenuItemIconMultipleSelection }} />
      }
    }

    const SetIconColor = (option) => {
      switch (true) {
        case (optionsWithColor.get(option) === 'transparent'):
          return <LabelOutline style={{ color: '#708090',}}/>
        case (optionsWithColor.get(option) === 'white'):
          return <LabelOutline />
        default:
          return <Label style={{ color: optionsWithColor.get(option), }} />
          break;
      }
    }

    const CustomControl = (
      <Select
        name='color'
        className={classes.rootSelectWrap}
        classes={{
          root: classes.rootSelect,
          select: classes.select,
        }}
        multiple
        autoWidth
        value={color}
        renderValue={() => this.selectionRenderer()}
        onChange={this.handleChange}
        disableUnderline
        displayEmpty
        MenuProps={{
          className: classes.selectedMenuPaper,
          anchorOrigin: {
            vertical: 'top',
            horizontal: 'left',
          },
          // for anchorOrigin:
          getContentAnchorEl: null,
          MenuListProps: {
            className: 'SelectedMenuList',
            classes: {
              padding: classes.MenuPropsListPadding
            }
          },
          BackdropProps: {
            invisible: false,
            className: classes.backDropRoot
          },
        }}
      >
        <MenuItem
          classes={{
            root: classes.MenuItemDisabled,
          }}
          disabled
          divider
          dense
        >
          <span className={classes.MenuItemDisabledText}>color: </span><em className={classes.MenuItemDisabledHook}>{color}</em>
        </MenuItem>
        <MenuItem
          value={null}
          dense
          divider
        >
          <span className={classes.MenuItemText}>ALL</span>
          {SetIcon(null)}
        </MenuItem>
        {options.map(option => (
          <MenuItem
            key={option}
            value={option}
            dense
          >
            {/* {SetIcon(option)} */}
            {SetIconColor(option)}
            <span className={`${classes.MenuItemText} filtersMenuItemText`}> {option} </span>
            {SetIcon(option)}
          </MenuItem>
        ))}
      </Select>
    )

    const NativeControl = (
      
      <div className={classes.rootNativeControl}>
        
        <InputLabel
          className={classes.nativeControlInputLabel}
          focused={false}
          shrink
          // htmlFor="select-color"
        >
          color
        </InputLabel>
          {/* <ListItemText
            // className={`${classes.MenuItemText} filtersMenuItemText`}
            className={classes.MenuItemText}
            disableTypography={true}
            primary={this.selectionRenderer(true)}
          /> */}
        {/* <Input
          classes={{
            // root: classes.nativeControlColorButton,
            root: classes.nativeColorRootInput,
            input: classes.nativeColorInput,
          }}
          onClick={this.handleDialogOpen}
          disableUnderline
          endAdornment={
            <InputAdornment className={classes.nativeColorInputAdornment}>
              <ArrowDropDownIcon />
            </InputAdornment>
          }
        /> */}

        <div className={classes.rootNativeColorSelect}>
        <button className={classes.nativeColorInputHTML} onClick={this.handleDialogOpen}/>
          <div 
            className={classes.nativeColorInput}
            // onClick={ this.handleDialogOpen}
          > 
            {this.selectionRenderer(true)}
          </div>
          <InputAdornment className={classes.nativeColorInputAdornment}>
            <ArrowDropDownIcon />
          </InputAdornment>
        </div>
        
  
        <Dialog
          classes={{
            paperWidthSm: classes.dialogPaperWidthSm,
          }}
          // classes={{root:classes.nativeColorDialogTitle}}
          // disableBackdropClick
          // disableEscapeKeyDown
          open={this.state.openDialog}
          onClose={this.handleDialogOpen}
        >
          <DialogTitle
            className={classes.nativeColorDialogTitle}
            disableTypography
          >
            <span>COLORS</span>
            <ListItem
              className={classes.nativeColorDialogTitleListItem}
              // classes={{
              //   root: classes.listItemNested,
              // }}
              button
              value={null}
              // dense
              // divider
              onClick={() => this.handleListItemClick(null)}
            >
              <ListItemText
                className={`${classes.MenuItemText} filtersMenuItemText`}
                disableTypography={true}
                primary='ALL'
              />
              <Checkbox checked={color.indexOf(null) > -1} />
            </ListItem>
          </DialogTitle>
          <Divider/>
          <DialogContent
            className={classes.nativeColorDialogContent}  
          >
            {/* <FormControl> */}
              {/* <InputLabel htmlFor="for-sortIcon">sort by</InputLabel> */}
              {/* <Select
                value={this.props.order}
                onChange={(e) => { this.props.fetchOrder(e.target.value); this.props.fetchImages() }}
                input={<Input id="for-sortIcon" />}
              > */}
              <List className={classes.nativeColorDialogContentList} component="div" disablePadding>
                {/* <ListItem
                  classes={{
                    root: classes.listItemNested,
                  }}
                  button
                  value={null}
                  // dense
                  divider
                  onClick={() => this.handleListItemClick(null)}
                >
                  <ListItemText
                    className={`${classes.MenuItemText} filtersMenuItemText`}
                    disableTypography={true}
                    primary='ALL'
                  />
                  <Checkbox checked={color.indexOf(null) > -1} />
                </ListItem> */}
                {options.map(option => (
                  <ListItem
                    // classes={{
                    //   root: classes.listItemNested,
                    // }}
                    button
                    key={option}
                    value={option}
                    // dense
                    onClick={() => this.handleListItemClick(option)}
                  >
                    {SetIconColor(option)}
                    <ListItemText
                      className={`${classes.MenuItemText} filtersMenuItemText`}
                      disableTypography={true}
                      primary={option}
                    />
                    <Checkbox checked={color.indexOf(option) > -1} />
                  </ListItem>
                ))}
              </List>
              {/* </Select> */}
            {/* </FormControl> */}
          </DialogContent>
        </Dialog>


        {/* <FormControl>
          <InputLabel
            className={classes.nativeControlInputLabel}
            focused={false}
            shrink
            htmlFor="select-color"
          >
            color
          </InputLabel>
          <Select
            name='color'
            className={`${classes.rootSelectWrap} ${classes.selectedValue}`}
            classes={{
              root: classes.rootSelect,
              select: classes.select,
            }}
            multiple
            autoWidth
            value={color}
            input={<Input id="select-color" />}
            renderValue={() => this.selectionRenderer(true)}
            onChange={this.handleChange}
            disableUnderline
            displayEmpty
            MenuProps={{
              className: classes.selectedMenuModal,
              classes: {
                paper: classes.selectedMenuPaperRoot,
              },
              MenuListProps: {
                className: 'SelectedMenuList',
                // classes: {
                //   padding: classes.MenuPropsListPadding,
                // }
              },
              BackdropProps: {
                invisible: false,
                className: classes.backDropRoot,
              },
            }}
          >
            <MenuItem
              value={null}
              // dense
              divider
            >
              <ListItemText 
                className={`${classes.MenuItemText} filtersMenuItemText`}
                disableTypography={true} 
                primary='ALL'
              />
              <Checkbox checked={color.indexOf(null) > -1} />
            </MenuItem>
            {options.map(option => (
              <MenuItem
                key={option}
                value={option}
                // dense
              >
                {SetIconColor(option)}
                <ListItemText 
                  className={`${classes.MenuItemText} filtersMenuItemText`}
                  disableTypography={true}
                  primary={option} 
                />
                <Checkbox checked={color.indexOf(option) > -1} />
              </MenuItem>
            ))}
          </Select>
        </FormControl> */}

        {/* <Button onClick={this.handleClickOpen}>Open select dialog</Button> */}
      </div>

    )

    const NativeControl_42 = (
      <div className={classes.rootNativeControl}>
        <InputLabel
          className={classes.nativeControlInputLabel}
          focused={false}
          shrink
          htmlFor="select-color"
        >
          color
        </InputLabel>
        <select
          // value={color}
          multiple
          // onChange={this.handleChange}
          onChange={(e, r, t) => { console.log('e', e), console.log('e.target', e.target), console.log('e.target.value', e.target.value), this.handleListItemClick(e.target.value)}}
          // onChange={(e, r, t) => { console.log('e', e), console.log('e.target', e.target), console.log('e.target.value', e.target.value) }}
        >
          <option disabled>color</option>
          <option 
            className={classes.nativeColorOption} 
            value={null}
            // onClick={() => this.handleListItemClick(null)}
            // id='exmpe'
            // style={{ background: 'url(/img/pixabayLogo2.svg) no-repeat right #0f0'}}
            // style={{backgroundColor:'#f00'}}
          >
            ALL
            {/* <Checkbox checked={color.indexOf(null) > -1} /> */}
          </option>
          {options.map(option => (
            <option 
              key={option} 
              className={classes.nativeColorOption}
              value={option}
              // onClick={() => this.handleListItemClick(option)}
            >
              {option}
              {/* <Checkbox checked={color.indexOf(option) > -1} /> */}
            </option>
          ))}
        </select>
      </div>
    )

    // set form type depends on device type (mobile devices will have native form selector)
    let formSelectorType;
    if (/Mobi/i.test(navigator.userAgent) || /Android/i.test(navigator.userAgent)) {
      formSelectorType = NativeControl
    } else {
      formSelectorType = CustomControl
    }

    // Fix flicker in Safari 9 when collapse finishes entering
    const removeTransition = node => {
      node.style.transition = '0s';
      requestAnimationFrame(() => {
        node.style.transitiіon = '';
      });
    };

    return (
      <FormControl className={classes.formControl}>

        {/* form type for screens with the smallest width resolution ('xs'/portrait) */}
        <List className={classes.rootListWrap}>
          <ListItem
            button
            classes={{
              root: classes.listItemHeader,
              button: classes.listItemHeaderButton,
            }}
            onClick={this.handleListHeaderClick}
          >
            <ListItemText
              classes={{ primary: classes.listItemTextHeader }}
              primary={
                this.state.open ? <span>color: </span> : this.selectionRenderer()
              }
            />
            {this.state.open ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={(showFiltersBar && this.state.open)} timeout="auto" unmountOnExit onEntered={removeTransition}>
            <List component="div" className={classes.listCollapsedItems} disablePadding>
              <ListItem
                button
                classes={{
                  root: classes.listItemNested,
                  button: classes.listItemNestedButton,
                }}
                onClick={() => this.handleListItemClick(null)}
                divider
              >
                <ListItemText
                  // inset
                  classes={{ primary: classes.listItemTextNested }}
                  primary={<span>ALL</span>}
                />
                {SetIcon(null)}
              </ListItem>
              {options.map(option => (
                <ListItem
                  key={option}
                  button
                  classes={{
                    root: classes.listItemNested,
                    button: classes.listItemNestedButton,
                  }}
                  onClick={() => this.handleListItemClick(option)}
                >
                  {SetIconColor(option)}
                  <ListItemText
                    inset
                    classes={{ primary: classes.listItemTextNested }}
                    primary={<span>{option}</span>}
                  />
                  {SetIcon(option)}
                </ListItem>
              ))}
            </List>

          </Collapse>
        </List>

        {/* form type for all another screens */}
        {formSelectorType}

      </FormControl>
    )
  }
}

ImgColor.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchColor: PropTypes.func.isRequired,
  color: PropTypes.array.isRequired,
  showFiltersBar: PropTypes.bool,
}

const mapStateToProps = state => ({
  color: state.search.color,
})

export default connect(mapStateToProps, { fetchImages, fetchColor })(compose(withStyles(styles), withWidth(), )(ImgColor))


