import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { fetchImages } from '../actions/searchAction';
import { fetchSearchText } from '../actions/searchAction';
import { fetchWindowWidth } from '../actions/searchAction';

import MenuDrawer from './Drawer';
import SearchFilters from './Filters';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import FormControl from '@material-ui/core/FormControl';
import AppBar from '@material-ui/core/AppBar';
import Divider from '@material-ui/core/Divider';

import Search from '@material-ui/icons/Search';
import Menu from '@material-ui/icons/Menu';
import MoreVert from '@material-ui/icons/MoreVert';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import Close from '@material-ui/icons/Close';
import FilterList from '@material-ui/icons/FilterList';

const styles = theme => ({
  appBar: {
    backgroundColor: 'rgb(38,50,56)',
    height: 57,
    padding: '0px 4px',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    borderBottom: '1px solid #ddd',
    transition: 'border-bottom 0.2s ease',
  },
  appBarBorder: {
    borderBottom: '1px solid rgb(38, 50, 56)',
    [theme.breakpoints.only('xs')]: {
      borderBottom: '1px solid #ddd',
    },
  },
  appBarInputGroup: {
    marginLeft: 4,
    width: '75%',
    display: 'flex',
    alignItems: 'center',
    [theme.breakpoints.only('xs')]: {
      width: '100%',
    },
  },
  appBarLastChildGroup: {
    display: 'inline-flex',
  },
  appBarIcons: {
    color: '#fff',
  },
  filtersButton: {
    // height: '1.6em',
    // width: '1.6em',
    // padding: '0',
    // transition: 'all 0.3s ease',
    height: '1.8em',
    width: '1.8em',
    transition: 'visibility 0.2s ease',
    [theme.breakpoints.only('xs')]: {
      width: '2em',
      height: '2em',
    },
    '& svg': {
      transition: 'all 0.2s ease',
    },
  },
  filtersButtonTop: {
    visibility: 'hidden',
    '& svg': {
      color: 'rgb(38, 50, 56)',
    },
    [theme.breakpoints.only('xs')]: {
      visibility: 'visible',
      '& svg': {
        color: '#fff',
      },
    },
  },
  deleteSearchTextButton: {
    height: '1.6em',
    width: '1.6em',
    padding: '0',
    transition: 'visibility 1s ease',
    visibility: 'hidden',
  },
  deleteSearchTextButtonDisplay: {
    visibility: 'visible',
  },
  deleteSearchTextIcon: {
    color: 'rgb(38, 50, 56)',
  },
  // filtersIcon: {
  //   color: '#263238',
  // },
  rootComponent: {
    position: 'fixed',
    width: '100%',
    top: 0,
    zIndex: 1001,
    transition: 'all 0.3s',
  },
  rootComponentHide: {
    transform: 'translate(0px, -57px)',
    [theme.breakpoints.only('xs')]: {
      transform: 'none',
    }
  },
  searchFieldFormControl: {
    width: '100%',
  },
  searchFieldInput: {
    padding: '6px 0px 4px',
    width: '100%',
  },
  searchFieldInputRoot: {
    paddingLeft: 5,
    fontSize: '1.2em',
    lineHeight: '1.2em',// 2.em
    height: '2em',
  },
  searchFiltersTop: {
    position: 'relative',
    top: 57,
    [theme.breakpoints.only('xs')]: {
      display: 'none',
    },
  },
  searchFiltersTopHidden: {
    visibility: 'hidden',
  },
  searchFiltersBody: {
    position: 'fixed',
    top: 0,
    height: 57,
    width: '100%',
    display: 'flex',
    alignItems: 'end',
    transition: 'all 0.3s',
    [theme.breakpoints.only('xs')]: {
      top: -200,
    },
  },
  searchFiltersBodyTransform: {
    transform: 'translate(0px, 57px)',
    [theme.breakpoints.only('xs')]: {
      transform: 'translate(0px, 257px)',
    },
  },
  searchFieldWrap: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: '100%',
    height: '2em',
    backgroundColor: '#f5f5f5',
    borderRadius: 1,
  },
})

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showDrawer: false,
      showFilters: false,
      windowTop: true,
      windowPageYOffset: 0,
      hideHeader: false,

    };
    this.handleResize = this.handleResize.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.handleDeleteSearchText = this.handleDeleteSearchText.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('scroll', this.handleScroll);
    this.props.fetchImages();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleResize() {
    let width = window.innerWidth;
    let currentWidth;

    switch (true) {
      case (width >= 1600):
        currentWidth = 1
        break;
      case (width >= 1024):
        currentWidth = 2
        break;
      case (width >= 768):
        currentWidth = 3
        break;
      case (width >= 480):
        currentWidth = 4
        break;
      default:
        currentWidth = 5
    }

    if (this.props.designType === currentWidth) {
      return null
    } else {
      (currentWidth === 5) ? (this.setState({ showFilters: false })) : null;
      this.props.fetchWindowWidth(currentWidth)
    }
  }

  handleScroll() {
    let currentYOffset = window.pageYOffset;
    let prevYOffset = this.state.windowPageYOffset;
    let hideHeader = this.state.hideHeader;
    if (currentYOffset < 40) {
      this.setState({
        windowTop: true,
        // showFilters: false,
      })
    } else {
      this.setState({
        windowTop: false,
        windowPageYOffset: currentYOffset,
      });

      // define mobile devices with touch screen for hiding NavBar on Scroll Down (and showing it on Scroll Up)
      if (/Mobi/i.test(navigator.userAgent) || /Android/i.test(navigator.userAgent)) {
        switch (true) {
          case (((currentYOffset - prevYOffset) > 10) && !hideHeader):
            this.setState({
              hideHeader: !hideHeader,
              showFilters: false,
            })
            break;
          case (((currentYOffset - prevYOffset) < -10) && hideHeader):
            this.setState({
              hideHeader: !hideHeader,
            })
            break;
          default:
            break;
        }
      } 
    }
  }

  handleMenuButtonClick = () => {
    this.setState({
      showDrawer: !this.state.showDrawer,
    })
  }

  handleFiltersButtonClick = () => {
    this.setState({
      showFilters: !this.state.showFilters,
    })
  }

  handleDeleteSearchText = () => {
    this.props.fetchSearchText('');
    this.props.fetchImages();
    this.searchTextInput.focus();
  }

  render() {
    const { classes, width, location } = this.props;

    let searchHint = 'Search...';

    let filtersType,
        filtersIcon,
        navigationMenuIcon,
        navigationMoreVertIcon,
        searchIcon,
        sortIcon,
        searchFiltersTop;

    switch (true) {
      case (location.pathname === '/'):
        filtersType = <SearchFilters showFiltersBar={this.state.showFilters} />
        break;
      case (location.pathname === '/favorites'):
        filtersType = null
        break;
      default:
        break;
    }

    const _deleteSearchTextIcon = (
      <IconButton
        onClick={this.handleDeleteSearchText}
        className={`${classes.deleteSearchTextButton} ${(this.props.searchText.length > 0) ? classes.deleteSearchTextButtonDisplay : null}`}
      >
        {/* {this.state.showFilters ? <ExpandLess className={classes.filtersIcon} /> : <ExpandMore className={classes.filtersIcon} />} */}
        <Close className={classes.deleteSearchTextIcon}/>
        {/* <Close className={classes.filtersIcon}/> */}
      </IconButton>
    );

    const _filtersIcon = (
      <IconButton
        onClick={this.handleFiltersButtonClick}
        className={`${classes.filtersButton} ${this.state.windowTop ? classes.filtersButtonTop : null }`} 
        // style={{ visibility: props }}
      >
        {/* {this.state.showFilters ? <ExpandLess className={classes.filtersIcon} /> : <ExpandMore className={classes.filtersIcon} />} */}
        <FilterList className={classes.appBarIcons} />
      </IconButton>
    );

    const _navigationMenuIcon = (
      <IconButton
        onClick={this.handleMenuButtonClick}
      >
        <Menu className={classes.appBarIcons} />
      </IconButton>
    );

    const _navigationMoreVertIcon = (
      <IconButton
      // onClick={this.handleMenuButtonClick}
      >
        <MoreVert className={classes.appBarIcons} />
      </IconButton>
    );

    const _searchIcon = (
      <IconButton
        onClick={() => this.searchTextInput.focus()}
      >
        <Search className={classes.appBarIcons} />
      </IconButton>
    );

    const _searchFiltersTop = (
      <div className={`${classes.searchFiltersTop} ${this.state.showFilters ? classes.searchFiltersTopHidden : null}`}>
        {/* <SearchFilters /> */}
        {filtersType}
      </div>
    );

    const _searchFiltersBody = (
      <div className={`${classes.searchFiltersBody} ${this.state.showFilters ? classes.searchFiltersBodyTransform : null}`} >
        {/* <SearchFilters /> */}
        {filtersType}
      </div>
    );

    if (this.props.designType === 5) {
      searchIcon = null;
      navigationMenuIcon = true;
      navigationMoreVertIcon = false;
    } else {
      searchIcon = _searchIcon;
      navigationMenuIcon = false;
      navigationMoreVertIcon = true;
    }

    const menuDrawer = (
      <MenuDrawer
        value={this.state.showDrawer}
        side={navigationMenuIcon ? 'right' : 'left'}
        onChange={(val) => this.setState({ showDrawer: val })}
      />
    );

    console.log(this.props.location);
    console.log('NavBar state: ', this.state);

    // // set form type depends on device type (mobile devices will have native form selector)
    // let touch;
    // if (/Mobi/i.test(navigator.userAgent) || /Android/i.test(navigator.userAgent)) {
    //   touch = true
    // } else {
    //   touch = false
    // }

    return (
      <div>
        <div className={`${classes.rootComponent} ${this.state.hideHeader ? classes.rootComponentHide : null}`}>
          <AppBar className={`${classes.appBar} ${(this.state.windowTop || this.state.showFilters) ? classes.appBarBorder : null}`}>
            {(!navigationMenuIcon) ? _navigationMenuIcon : null}
            <div className={classes.appBarInputGroup}>
              {searchIcon}
              <div className={classes.searchFieldWrap}>
                <FormControl className={classes.searchFieldFormControl} //onSubmit={() => { touch ? this.props.fetchImages() : null }}
                >
                  <Input
                    classes={{
                      root: classes.searchFieldInputRoot,
                      input: classes.searchFieldInput,
                    }}
                    value={this.props.searchText}
                    onChange={(e) => { this.props.fetchSearchText(e.target.value); this.props.fetchImages() }}
                    // onChange={(e) => { this.props.fetchSearchText(e.target.value); touch ? null : this.props.fetchImages() }}
                    placeholder={searchHint}
                    disableUnderline={true}
                    autoFocus
                    inputRef={input => this.searchTextInput = input}
                    // title='42'
                  />
                  {/* <submit returnKeyType={'Search'} type='submit' title='42' name='42' style={{visibility:'hidden', position:'absolute'}}/> */}
                </FormControl>
                {_deleteSearchTextIcon}
              </div>
              {_filtersIcon}
            </div>
            <div className={classes.appBarLastChildGroup}>
              {/* {_navigationMoreVertIcon} */}
              {navigationMoreVertIcon ? _navigationMoreVertIcon : null}
              {navigationMenuIcon ? _navigationMenuIcon : null}
            </div>
          </AppBar>
          {_searchFiltersBody}
          {this.state.showDrawer ? menuDrawer : null}
        </div>
        {_searchFiltersTop}
      </div>
    )
  }
}

NavBar.propTypes = {
  fetchSearchText: PropTypes.func.isRequired,
  fetchImages: PropTypes.func.isRequired,
  fetchWindowWidth: PropTypes.func.isRequired,
  searchText: PropTypes.string.isRequired,
  designType: PropTypes.number.isRequired,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  searchState: state,
  searchText: state.search.searchText,
  designType: state.search.designType,
})

const mapDispatchToProps = {
  fetchImages,
  fetchSearchText,
  fetchWindowWidth,
}

export default connect(mapStateToProps, mapDispatchToProps)(compose(withStyles(styles), withWidth(), )(NavBar))


